import AppDispatcher from '../AppDispatcher';
import Constants from '../constants';

const baseUrl = 'https://blog.intelligentbee.com/wp-json/wp/v2/posts';

const PostActions = {

  fetch() {
    console.log('Called fetch()');
    fetch(baseUrl)
      .then( (response) => {
          return response.json() })
      .then( (json) => {
        AppDispatcher.dispatch({
          actionType: Constants.ACTIONS.POSTS.FETCHED,
          posts: json,
        });
      })
      .catch( (error) => {
        console.log(error);
      });
  },
};

export default PostActions;



