import AppDispatcher from '../AppDispatcher';
import Constants from '../constants';

const baseUrl = 'https://blog.intelligentbee.com/wp-json/wp/v2/users?per_page=100';

const UserActions = {

  fetch() {
    console.log('Called fetch()');
    fetch(baseUrl)
      .then( (response) => {
          return response.json() })
      .then( (json) => {
        AppDispatcher.dispatch({
          actionType: Constants.ACTIONS.USERS.FETCHED,
          users: json,
        });
      })
      .catch( (error) => {
        console.log(error);
      });
  },
};

export default UserActions;



