import React, { Component } from 'react';

import Loader from './loader'
import PostList from './postList';
import PostActions from '../actions/postActions';
import UserActions from '../actions/userActions';
import PostStore from '../stores/postStore';

class PostListTimeline extends Component {
  constructor(props) {
    super(props);
    this.state = { isLoaded: PostStore.getHasFetchedCollection(),
                   data: PostStore.getCollection()
                 };
  }

  ipStoreChanged = () => {
    this.setState({ data: PostStore.getCollection(), isLoaded: PostStore.getHasFetchedCollection() });
  }

  componentDidMount = () => {
    console.log('Child componentDidMount');
    PostStore.addChangeListener(this.ipStoreChanged);
    UserActions.fetch();
    PostActions.fetch();
  }

  componentWillUnmount = () => {
    PostStore.removeChangeListener(this.ipStoreChanged);
  }

  render() {
    console.log('render() called with this state:');
    console.log(this.state);
    return (
      <div className="row">
        {PostStore.getHasFetchedCollection() ? (
          <PostList data={this.state.data} />
        ) : (
          <Loader />
        )}
      </div>
    );
  }
}

export default PostListTimeline;



