import _ from 'underscore';
import EventEmitter from 'eventemitter2';

import Constants from '../constants';
import AppDispatcher from '../AppDispatcher';

let collection = [];
let hasFetchedCollection = false;

const initState = () => {
  hasFetchedCollection = false;
  collection = [];
};

const setCollection = (c) => {
  collection = c;
  hasFetchedCollection = true;
};

const addItem = (item) => {
  collection.push(item);
};

const removeItem = (id) => {
  collection = _.reject(collection, item => item.id === id);
};

const UserStore = _.extend({}, EventEmitter.prototype, {
  getHasFetchedCollection() {
    return hasFetchedCollection;
  },

  getCollection() {
    // Return a clone -- the data from the store should be immutable.
    return JSON.parse(JSON.stringify(collection));
  },

  getById(id) {
    if (!id) return undefined;
    return _.find(collection, c => c.id && c.id.toString() === id.toString());
  },

  addChangeListener(callback) {
    this.on(Constants.EVENTS.STORES.CHANGE, callback);
  },

  removeChangeListener(callback) {
    this.off(Constants.EVENTS.STORES.CHANGE, callback);
  },

  emitChange() {
    this.emit(Constants.EVENTS.STORES.CHANGE);
  },

  _test_initState: () => initState(),

  dispatcherCb(action) {
    console.log(action);
    switch (action.actionType) {
      case Constants.ACTIONS.USERS.FETCHED:
        setCollection(action.users);
        UserStore.emitChange();
        break;
      case Constants.ACTIONS.USERS.CREATED:
        addItem(action.id);
        UserStore.emitChange();
        break;
      case Constants.ACTIONS.USERS.DELETED:
        removeItem(action.id);
        UserStore.emitChange();
        break;
      default:
        break;
    }
  },
});

initState();
UserStore.dispatchToken = AppDispatcher.register(UserStore.dispatcherCb);
window.AppDispatcher = AppDispatcher;
window.UserStore = UserStore;
export default UserStore;



