import React from 'react';
import PropTypes from 'prop-types';

function Gravatar(props) {
  return (
    <img className="img-circle" src={props.url} alt="" />
  );
};

Gravatar.propTypes = {
  url: PropTypes.string.isRequired
};

export default Gravatar;



