import React from 'react';
import PropTypes from 'prop-types';
import * as moment from 'moment';

import Gravatar from '../gravatar';
import PLoader from '../ploader'

function createMarkup(content) {
  return {__html: content};
}

function formatDate(date) {
  return moment(date).format("MMM Do");
}

function Post(props) {
  return (
    <div className="cd-timeline-block">
      <div className="cd-timeline-img cd-picture">
      {props.gravatar_url ? (
        <Gravatar url={props.gravatar_url} />
      ) : (
        <PLoader />
      )}

      </div>
      <div className="cd-timeline-content">
        <h2>{props.title}</h2>

        <div dangerouslySetInnerHTML={createMarkup(props.excerpt)} />

        <a href={props.link} rel="noopener noreferrer" target="_blank" className="cd-read-more">Read more</a>
        <span className="cd-date">{formatDate(props.date_gmt)}</span>
      </div>
    </div>
  );
};

Post.propTypes = {
  id: PropTypes.number.isRequired,
  date: PropTypes.string.isRequired,
  date_gmt: PropTypes.string.isRequired,
  slug: PropTypes.string.isRequired,
  link: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  excerpt: PropTypes.string.isRequired,
  author_id: PropTypes.number.isRequired,
  gravatar_url: PropTypes.string,
};

export default Post;



